

.App {
  display: grid;
  grid-template-columns: auto 200px;
  grid-template-rows: 20px 20px;
  gap: 20px;
  max-width: 10em;
  margin: 0 auto;
  min-height: 50vh;
  padding: 20px;
  position: absolute;
  top: 50vh;
  right: 10px;
  border: solid black;
  background: rgb(255, 255, 255, 0.75);
}

.Column {
  color: black;
}

.Title {
  padding: 20px;
  margin-bottom: 20px;
  border-radius: 10px;
  color: black;
  background-color: rgb(170, 174, 255);
}

.Content {
  height: 5em;
  overflow-y: scroll;
  margin-bottom: 20px;
}

::-webkit-scrollbar {
  display: none;
}